<template>
	<div class="Item">
		<div class="Item_address" @click="linkage">
			联动
		</div>
		<div class="Item_center">
			<router-link to="/hot">正在热映</router-link>
			<router-link to="/replace">即将上映</router-link>
		</div> 
		<div class="Item_search">
			<i class="fa fa-search" id="Icon"></i>
		</div>
	</div>
</template>

<script>
	export default{
		name:"Item",
		methods:{
			linkage(){
				this.$router.push("/linkage")
			}
		}
	}
</script>

<style scoped>
	.Item{
		width: 100%;
		height: 44px;
		line-height: 44px;
		font-size: 20px;
	}
	.Item>div{
		float: left;
	}
	.Item>.Item_address{
		display: flex;
		justify-content: center;
		width: 25%;
		height: 100%;
		text-align: center;
	}
	.Item>.Item_center{
		display: flex;
		width: 50%;
		height: 100%;
		text-align: center;
	}
	.Item>.Item_center a{
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 50%;
		color: #000000;
	}
	.Item>.Item_search{
		display: flex;
		width: 25%;
		height: 100%;
		justify-content: center;
	}
	.Item>.Item_search #Icon{
		height: 44px;
		line-height:44px;
		color:#EF4238
	}
</style>


