<template>
	<div id="sy">
		<Item class="navBar"/>
		<!--exclude    指定缓存-->
		<keep-alive exclude="">  
			<router-view class="content"/>
		</keep-alive>
	</div>
</template>

<script>
	import Item from "./Item"
	export default {
		
		components:{
			
			Item
		}
	}
</script>

<style scoped>

	.navBar{
		position: fixed;
		top:49px;
		left:0;
		right: 0;
		z-index: 2;
		background: #fff;
	}
</style>


